<template>
  <profile-template>
    <div class="mt-12">
      <input-field
        id="password"
        ref="password"
        label="Digite sua nova senha"
        type="password"
        icon="lock"
        :success="isPasswordValid(password)"
        :rules="passwordRules"
        @onKeyUp="onKeyUp" 
        @onKeyUpEnter="focusToNext('confirmPassword')" 
      />
      <input-field
        ref="confirmPassword"
        id="confirmPassword"
        label="Confirme a senha"
        type="password"
        icon="lock"
        :success="isPasswordValid(confirmPassword) && confirmPassword === password"
        :rules="confirmPasswordRules"
        @onKeyUp="onKeyUp" 
      />
    </div>
    <div class="mt-8 mb-4 mx-sm-12 text-center">
      <btn-secondary
        text="Salvar"
        dark
        :disabled="btnDisabled"
        :loading="loading"
        @clicked="save"
      />
      <btn-text
        class="mt-4"
        text="Cancelar"
        @clicked="goToBack"
      />
    </div>
  </profile-template>
</template>

<script>
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import BtnText from '@/components/globais/buttons/text/BtnText'
import InputField from '@/components/globais/form/input-field/InputField'
import ProfileTemplate from '@/components/site/ProfileTemplate'
import FormMixin from '@/mixins/FormMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'ChangePasword',
  components: { BtnSecondary, BtnText, InputField, ProfileTemplate },
  mixins: [ FormMixin, NavigationMixin, SnackbarMixin, ValidationMixin ],
  data: (vm) => ({
    loading: false,
    password: null,
    confirmPassword: null,
    confirmPasswordRules: [
      (v) => v === vm.password || 'Senha diferente da informada.',
    ]
  }),
  computed: {
    btnDisabled () {
      if (
        !this.password || 
        !this.confirmPassword || 
        this.password !== this.confirmPassword ||
        this.password.length < 6
      ) return true
      return false
    }
  },
  methods: {
    onKeyUp (value, id) {
      this.$data[id] = value
    },
    save () {
      this.loading = true
      this.$axios.post(`user/change-password`, { password: this.password })
      .then(response => {
        if (response.data.codigo === 200) {
          this.showSnackbar('Senha alterada com sucesso!')
          this.goToBack()
        } else this.setErro(response.data.corpo)
      })
      .catch (() => this.setErro('Não foi possível alterar a senha!'))
    },
    setErro (msg) {
      this.loading = false
      this.showSnackbarErro(msg)
    }
  }
}
</script>